import React from 'react';
import { Container, Accordion, Row, Col, Card } from 'react-bootstrap';
import { Book, Brain, BarChart2 } from 'lucide-react'; // Using Lucide icons
import { useData } from '../components/DataLoader';
import './Tools.css';

const CategoryIcon = ({ category }) => {
  switch (category) {
    case 'psycholinguistique':
      return <Brain size={48} />;
    case 'dictionnaires':
      return <Book size={48} />;
    case 'statistiques':
      return <BarChart2 size={48} />;
    default:
      return null;
  }
};

const categories = {
  'psycholinguistique': 'Outils psycholinguistiques',
  'dictionnaires': 'Dictionnaires et lexiques',
  'statistiques': 'Statistiques'
};

const Tools = () => {
  const { data, loading, error } = useData();

  if (loading) return <div>Chargement...</div>;
  if (error) return <div>Erreur de chargement</div>;

  const toolsByCategory = data.tools.reduce((acc, tool) => {
    const category = tool.categorie?.trim().toLowerCase();
    if (category && category !== '') {
      if (!acc[category]) {
        acc[category] = [];
      }
      acc[category].push(tool);
    }
    return acc;
  }, {});

  return (
    <Container className="my-5">
      <h1 className="mb-4">Nos Outils</h1>

      <br />
      
      <Accordion>
        {Object.entries(toolsByCategory).map(([category, tools]) => (
          <Accordion.Item eventKey={category} key={category}>
            <Accordion.Header>
              <div className="category-header">
                <CategoryIcon category={category} />
                <span>{categories[category] || category}</span>
                <span className="ms-2 text-muted">({tools.length})</span>
              </div>
            </Accordion.Header>
            <Accordion.Body className="tools-container">
              <Row className="g-4">
                {tools.map((tool, idx) => (
                  <Col md={6} lg={4} key={idx}>
                    <Card className="tool-card h-100">
                      <Card.Body>
                        <Card.Title className="tool-title">{tool.nom}</Card.Title>
                        <Card.Text className="tool-description">
                          {tool.description}
                        </Card.Text>
                        <a 
                          href={tool.lien} 
                          className="tool-link"
                          target="_blank" 
                          rel="noopener noreferrer"
                        >
                          Accéder à l'outil
                        </a>
                      </Card.Body>
                    </Card>
                  </Col>
                ))}
              </Row>
            </Accordion.Body>
          </Accordion.Item>
        ))}
      </Accordion>

    </Container>
  );
};

export default Tools;